.myworkMain{
    background-color: black;
    min-height: 100vh;
    padding: 3vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  
  .myworkMain h1{
    padding-top: 8vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 3.5rem;
    font-weight: 700;
    height: 20vh;
  }
  
  .myworkContainer{
    margin-top: 2vh;
    padding: 3vw;
    display:grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
    grid-gap :10vw;
    justify-content: center;
    align-items: center;
    justify-items: center;
  
    overflow: hidden;
  }
  
  @media screen and (max-width:768px){
    .myworkMain h1{
      font-size: 2.5rem;
    }
  }